.detailsInfoBox {
	border-radius: 4px;
	padding: 16px;
	margin-bottom: 24px;
}

.accountValueInfo {
	padding: 8px 0;
	display: flex;
	align-items: center;
}

.copy {
	cursor: pointer;
}

.action {
	border: 1px solid var(--border-color-2);
	border-radius: var(--border-radius);
	background: var(--bg-white);
	cursor: pointer;
	justify-content: space-between;
	padding: 0 16px;
	margin-bottom: 16px;
	height: 64px;
}